@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
html,body{margin:0;padding:0;font-family:"IBM Plex Sans",sans-serif;background-image:linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%)}.home{display:grid;place-items:center;height:100vh;grid-template-columns:repeat(1, minmax(0, 1fr))}@media screen and (min-width: 768px){.home{grid-template-columns:repeat(2, minmax(0, 1fr))}}.home .title{font-size:3rem;font-family:"IBM Plex Mono",monospace;color:black}@media screen and (min-width: 768px){.home .title{font-size:5rem}}.home .features{display:grid;place-items:center;grid-gap:2rem;gap:2rem}.home .uploadForm,.home .getForm{display:grid;place-items:center;grid-gap:1rem;gap:1rem;background-color:whitesmoke;border-radius:20px;padding:3rem;box-shadow:0 2.8px 2.2px rgba(0,0,0,0.02),0 6.7px 5.3px rgba(0,0,0,0.028),0 12.5px 10px rgba(0,0,0,0.035),0 22.3px 17.9px rgba(0,0,0,0.042),0 41.8px 33.4px rgba(0,0,0,0.05),0 100px 80px rgba(0,0,0,0.07)}@media screen and (min-width: 768px){.home .uploadForm,.home .getForm{padding:6rem 4rem}}.home .uploadForm__error{color:black;font-weight:bold;background-color:lightcoral;border-radius:20px;padding:1rem;margin:1rem 0}.home .uploadForm .uploadLabel,.home .getForm .uploadLabel{font-family:"IBM Plex Mono",monospace;font-size:1.2rem}@media screen and (min-width: 768px){.home .uploadForm .uploadLabel,.home .getForm .uploadLabel{font-size:1.5rem}}.home .uploadForm .fileInput,.home .getForm .fileInput{font-family:"IBM Plex Sans",sans-serif;background-color:white;border-radius:0 20px 20px 0}.home .uploadForm .submitBtn,.home .getForm .submitBtn{font-size:0.875rem;border:none;border-radius:20px;padding:0 1rem;min-width:88px;height:32px;font-family:"IBM Plex Sans",sans-serif;font-weight:bold;background-color:aquamarine;cursor:pointer}.home .uploadForm .submitBtn:focus,.home .getForm .submitBtn:focus{outline:none;box-shadow:0 2.8px 2.2px rgba(0,0,0,0.02),0 6.7px 5.3px rgba(0,0,0,0.028),0 12.5px 10px rgba(0,0,0,0.035),0 22.3px 17.9px rgba(0,0,0,0.042),0 41.8px 33.4px rgba(0,0,0,0.05),0 100px 80px rgba(0,0,0,0.07);transform:scale(0.9)}.home .getForm__error{color:black;font-weight:bold;background-color:lightcoral;border-radius:20px;padding:1rem;margin:1rem 0}.home .getForm .getLabel{font-family:"IBM Plex Mono",monospace;font-size:1.2rem}@media screen and (min-width: 768px){.home .getForm .getLabel{font-size:1.5rem}}.home .getForm .filenameInput{border-radius:20px;border:1px solid grey;padding:0.5rem 1rem}.home .getForm .filenameInput:focus{outline:none}

